.movie {
  color: rgba(250, 240, 240, 0.788);
  display: flex;
  justify-content: center;
}

/* flex-row : show divs in a row */
.show {
  display: flex;
  flex-direction: row;
  margin: 10px 10px;
  padding: 10px ;
  width: 500px;
  height: 370px;
  border: 2px solid snow;
  border-radius: 10px;
  overflow: hidden;
}

/* float:left -> U can displace the letters
right side of Img */
.Img {
  float: left;
  width: 230px;
  height: 345px;
}

.letters {
  width: 250px;
  height: 350px;
  padding-left: 20px;
}

.title {
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.letters h3,
.letters p {
  margin: 10px;
}

/* delete the style of li
(ex : - 1 - 2 - 3  =>  1 2 3) */
.letters li {
  list-style-type: none;
}

/* word-break : Lines are separated 
by number of lines, not words  */
.letters p {
  word-break: break-all;
  text-align: justify;
}

.summary {
  width: 100%;
  padding: 0 10px 10px 10px;
}