.container {
    height: 100%;
    padding-top: 30px;
  }
  
  .movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(500px, auto));
  }
  
  .footer {
  display: flex;
  justify-content: center;
  height: 100px;
  }
  
  .list {
  width: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  }
  
  .list a {
  font-size: 30px;
  }